
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;500;700;900&family=Poppins:wght@200;300;500;600;800&display=swap');
$background: #16161a;


@mixin flex {

    display: flex;
    justify-content: center;
    align-items: center;
    
    
}

@mixin textInput {
    
    height: 42px;
    text-decoration: none;
    background-color: rgb(255, 255, 255);
    border: none;
    border-radius: 8px 0px 0px 8px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    color:  #16161a;
    font-size: 22px;

}


@mixin iconsButtons {
    font-size: large;
    background-color: transparent;
    border: none;
    color: green
}

*{
    background-color: $background;
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    
}

.title{
    
    padding-top: 15vh;
    @include flex();
    width: 90%;
    color:#ffffff;
   
    height:auto;
    margin: auto;
    text-align: center;
   
}

.input-section{
    display: grid;
    grid-template-columns: 80% 20%;
    background-color:transparent;
    width: 90%;
    height: 10vh;
    padding: 10px;
    margin: auto;
    justify-content: space-around;
    border-radius: 8px;
    border: none;

    .task-adder{
        border-radius: 8px 0px 0px 8px;
        border: 1px solid rgb(71, 0, 151);
        background-color: transparent;
        padding-left: 10px;
        font-size: 20px;
        color: #fffffe;
        &:hover{
            
            background-color: rgba(102, 2, 233, 0.1);
            color: #fffffe;
            transition: 300ms;
        }

        &:focus{
            background-color: rgba(102, 2, 233, 0.1);
            border: 1px solid rgb(102, 0, 218);
            outline:none;
            color: #fffffe
            
        }

    }

    .add-btn{
        background-color:#7f5af0;
        border: 1px solid rgb(102, 0, 218);
        color: #fffffe;
        border-radius: 0px 8px 8px 0px;
        font-size: larger;

        &:hover {
            background-color: rgba(196, 116, 11, 0.692);
            transition: 300ms;
            cursor: pointer;
        }
        
    }
}


.task-container{
    width: 100%;
    border: none;
    background-color: transparent;
    padding-top: 10vh;
    @include flex();
    flex-direction: column;
    margin: auto;
}


.item-value{
    @include textInput();
     height: auto;

     &:focus{
        outline:none;
     }
}

.renderedItem{
    display: grid;
    grid-template-columns: 10%  70% 10% 10% ;
    width: 90%;
    height:40px;
    padding-block: 10px;
    padding: 5px;
    
   
}

// -----------------------------------------------------------------buttons


.delete-btn{
    border: none;
    border-radius: 0px 8px 8px 0px;
    background-color:white;
    font-size: 28px;
    color: rgb(163, 0, 0);
    
    
    &:hover{
        cursor: pointer;
        color: #eb720e;
        transition: 350ms;
        
       
    }
}


.edit-btn{
    border: none;
   
    background-color:white;
    font-size: 24px;
    color:rgb(7, 138, 182);
    &:hover{
        cursor: pointer;
        color: #0e2feb;
        transition: 350ms;
       
    }
}

.modify-btn{
    border: none;
   
    background-color:white;
    font-size: 27px;
    color:#2cb67d;
    &:hover{
        cursor: pointer;
        color:#2cb67d83;
        transition: 350ms;
       
    }
}

.completed-btn{
    border: none;
   margin: 5px;
   background-color: transparent;
    font-size: 42px;
    color:#0bfd98;
    &:hover{
        
        color:#80fd0b;
        transition: 350ms;
       
    }

}

//----------------------------------------------checkbox

//tablets
@media screen and (max-width: 768px) { // tablets
    .renderedItem{
        grid-template-columns: 12%  68% 10% 10% ;
        font-size: 10px;
        padding-block: 9px;
    }
    .item-value{
     font-size: large;  
    }
    
}


@media screen and (max-width: 480px) { // mob devices
    .renderedItem{
        grid-template-columns: 12%  68% 10% 10% ;
        font-size: medium;
        padding-block: 12px;
    }
    .item-value{
     font-size: large;  
    }
    
}
@media screen and (max-width: 400px) {
    .renderedItem{
        grid-template-columns: 15%  65% 10% 10% ;
        
    }
    .item-value{
        font-weight: 600;
       font-size: small;
    }
    
}